.container-entry .stone, .container-entry .leave > img, .container-entry .stars, .container-entry .progress {
  transform: translate(0, 0); }

.container-entry .title {
  animation: titleIn linear .5s forwards; }

.container-entry .leaf-rb1 {
  transform-origin: 100% 0; }

.container-entry .leaf-rt1, .container-entry .leaf-rt2, .container-entry .leaf-rb2 {
  transform-origin: 100% 100%; }

.container-entry .leaf-lt1, .container-entry .leaf-lt2 {
  transform-origin: 0 0; }

.container-entry .leaf-lb1, .container-entry .leaf-lb2 {
  transform-origin: 0 100%; }

.container-entry .leaf-lb1, .container-entry .leaf-lt1, .container-entry .leaf-rt1, .container-entry .leaf-rb2 {
  animation: shake-lb1 1s linear 0.3s infinite alternate forwards; }

.container-entry .leaf-lb2, .container-entry .leaf-lt2, .container-entry .leaf-rt2, .container-entry .leaf-rb1 {
  animation: shake-lb2 1s linear 0.3s infinite alternate forwards; }

.container-entry img, .container-entry .stars, .container-entry .title, .container-entry .progress {
  transition: transform linear .3s; }

.container-entry .stars .star1 {
  animation: shine 1s linear 1.5s infinite alternate forwards; }

.container-entry .stars .star2 {
  animation: shine 1s linear 1s infinite alternate forwards; }

.container-entry .stars .star3 {
  animation: shine 1s linear 0.8s infinite alternate forwards; }

.container-entry .stars .star4 {
  animation: shine 1s linear 1.2s infinite alternate forwards; }

.container-entry .stars .star5 {
  animation: shine 1s linear 1.5s infinite alternate forwards; }

.container-entry .stars .star6 {
  animation: shine 1s linear 0.3s infinite alternate forwards; }

.container-entry .stars .star7 {
  animation: shine 1s linear 0.5s infinite alternate forwards; }

.container-entry .stars .star8 {
  animation: shine 1s linear 0.8s infinite alternate forwards; }

.container-entry .box {
  animation: boxIn .4s ease-out forwards; }

.container-entry .box_line_1 {
  animation-delay: 0s; }

.container-entry .box_line_2 {
  animation-delay: 0.5s; }

.container-entry .box_line_3 {
  animation-delay: 1s; }

.container-entry .box_line_4 {
  animation-delay: 1.5s; }

.container-entry .opening .box {
  animation: rewards1 .3s linear 3 forwards; }

.container-entry .opened.boxItem .box {
  animation: none;
  transform: scale(1); }

.container-entry .opened.boxItem .unOpen {
  visibility: hidden; }

.container-entry .opened.boxItem .openedBox {
  visibility: visible; }

.container-entry .opened.boxItem .boxStar1 {
  animation: boxStar1 2s ease-out forwards; }

.container-entry .opened.boxItem .boxStar2 {
  animation: boxStar2 2s ease-out forwards; }

.container-entry .opened.boxItem .score {
  transform: scale(1) translate(-10px, -10px); }

.container-entry .opened.boxItem .box5 .score {
  transform: scale(0.6) translate(-10px, 10px); }

.container-entry .opened.boxItem .box10 .score {
  transform: scale(0.8) translate(-10px, 10px); }

.layer.showReady {
  visibility: visible; }
  .layer.showReady .ready {
    transform: scale(1) translate(-50%, -50%); }

.layer.showGo {
  visibility: visible; }
  .layer.showGo .go {
    transform: scale(1) translate(-50%, -50%); }
  .layer.showGo .btn {
    animation: btn .5s linear 1s infinite reverse forwards; }

@keyframes shake-lb2 {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(-2deg); } }

@keyframes shake-lb1 {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(2deg); } }

@keyframes boxIn {
  0% {
    transform: scale(0) translateY(0); }
  50% {
    transform: scale(0.5) translateY(0px); }
  80% {
    transform: scale(0.8) translateY(0px); }
  100% {
    transform: scale(1) translateY(30px); } }

@keyframes rewards1 {
  0% {
    transform: translate(0, 30px); }
  20% {
    transform: translate(2px, 32px); }
  40% {
    transform: translate(2px, 28px); }
  60% {
    transform: translate(-2px, 32px); }
  80% {
    transform: translate(2px, 28px); }
  100% {
    transform: translate(0, 30px); } }

@keyframes boxStar1 {
  0% {
    opacity: 1;
    transform: translate(0, 0) scale(0.1) rotate(0); }
  10% {
    opacity: 1;
    transform: translate(-40px, -20px) scale(0.25) rotate(180deg); }
  100% {
    opacity: 0;
    transform: translate(-40px, -30px) scale(0.25) rotate(540deg); } }

@keyframes boxStar2 {
  0% {
    opacity: 1;
    transform: translate(0, 0) scale(0.1) rotate(0); }
  10% {
    opacity: 1;
    transform: translate(40px, -20px) scale(0.25) rotate(180deg); }
  100% {
    opacity: 0;
    transform: translate(40px, -20px) scale(0.25) rotate(540deg); } }

@keyframes btn {
  0% {
    transform: scale(1); }
  49% {
    transform: scale(1); }
  50% {
    transform: scale(1.1); }
  100% {
    transform: scale(1.1); } }

@keyframes shine {
  from {
    opacity: 1; }
  to {
    opacity: 0.6; } }

@keyframes titleIn {
  0% {
    transform: translate(0, -100%); }
  50% {
    transform: translate(0, 8%); }
  75% {
    transform: translate(0, -4%); }
  100% {
    transform: translate(0, 0); } }
